import { INTERACTION_CLASS_NAME } from "./PersistedChat/Chat"

const CHAT_SCROLL_TOP_OFFSET = 76

const scrollToLastInteraction = () => {
  const all = window.document.querySelectorAll(`.${INTERACTION_CLASS_NAME}`)
  const last = all[all.length - 1]
  const interactionContainer = last?.getBoundingClientRect()

  interactionContainer &&
    window.scroll({
      top: window.scrollY + interactionContainer.y - CHAT_SCROLL_TOP_OFFSET,
      behavior: "smooth",
    })
}

export { scrollToLastInteraction, CHAT_SCROLL_TOP_OFFSET }
